import { Menu, Icon } from 'antd';
import React , {Component} from 'react';
import {Link} from 'react-router-dom';
import history from '../../../../history';
import './index.css';
const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;

class Sider extends Component {

  handleClick = (e) => {
    history.push( this.props.url+'/'+ e.key);
    window.location.reload();
  }


  render() {
    return (
      <Menu
        onClick={this.handleClick}
        style={{ width: 256 }}
        defaultSelectedKeys={['1']}
        defaultOpenKeys={['sub1', 'sub2']}
        mode="inline"
      >
        <SubMenu key="sub1" title={<span><Icon type="mail" /><span>Thng tin</span></span>}>
            <Menu.Item key="5">Cp nht mt khu</Menu.Item>
            <Menu.Item key="6">Cp nht thng tin</Menu.Item>
            <SubMenu key="sub3" title="Gii thiu">
                <Menu.Item key="7">H thng</Menu.Item>
                <Menu.Item key="8">C nhn</Menu.Item>
            </SubMenu>
        </SubMenu>
        <SubMenu key="sub2" title={<span><Icon type="appstore" /><span>Qun l ca admin</span></span>}>
          <MenuItemGroup key="g1" title="Module">
            <Menu.Item key="car">
                Tt c xe ca h thng
            </Menu.Item>

            <Menu.Item key="partner">
                  Qun l i tc
              </Menu.Item>

              <Menu.Item key="oders">
                  Qun l n hng
              </Menu.Item>

              <Menu.Item key="customer">
                  Qun l khch hng
              </Menu.Item>

              {/* <Menu.Item key="customercar">
                  Khch hng cho th xe
              </Menu.Item> */}

              <Menu.Item key="feature">
                  Tnh nng xe
              </Menu.Item>

              <Menu.Item key="packages">
                  Gi dch v
              </Menu.Item>

              <Menu.Item key="payment">
                  Phng thc thanh ton
              </Menu.Item>

              <Menu.Item key="procedure">
                  Th tc t xe
              </Menu.Item>

          </MenuItemGroup>
          
        </SubMenu>
        <SubMenu key="sub4" title={<span><Icon type="mail" /><span>Thng tin</span></span>}>
            <Menu.Item key="sub4item1">Cp nht mt khu</Menu.Item>
            <Menu.Item key="sub4item2">Cp nht thng tin</Menu.Item>
            <SubMenu key="sub41" title="Gii thiu">
                <Menu.Item key="sub41item1">H thng</Menu.Item>
                <Menu.Item key="sub41item2">C nhn</Menu.Item>
            </SubMenu>
        </SubMenu>
      </Menu>
    );
  }
}
export default Sider;